<template>
  <v-row v-if="applicableDurations.length > 0">
    <v-col cols="12">
      <v-select solo label="Select Duration" :disabled="loading" :value="selectedDuration" @change="changeDuration" :items="applicableDurations" return-object item-text="label">
      </v-select>
    </v-col>
  </v-row>
</template>
<script>
import {mapGetters, mapMutations} from 'vuex'
export default {
  name: "Durations",
  computed: {
    ...mapGetters([
        "selectedDuration",
        "getSelectedInstrument",
        "network",
        "loading",
        "durations"
    ])
  },
  data: function () {
    return {
      applicableDurations: []
    }
  },
  watch: {
    async network(oldValue, newValue) {
      if (oldValue !== newValue) {
        try {
          if (!this.network) return;
          this.init();
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    async getSelectedInstrument(oldValue, newValue) {
      if (oldValue !== newValue) {
        try {
          if (!this.network) return;
          this.init();
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    async account(oldValue, newValue) {
      if (oldValue !== newValue) {
        try {
          if (!this.network) return;
          this.init();
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    }
  },
  methods:{
    ...mapMutations([
        "setSelectedDuration",
        "startLoading",
        "stopLoading"
    ]),
    init: function (item = null) {
      let selectedDuration = item;
      if(item == null) {
        this.instrument = this.network.instrument[this.getSelectedInstrument];
        selectedDuration = this.durations.find(v => v.label === this.instrument.duration);
        this.applicableDurations = this.durations.filter(v => v.duration >= selectedDuration.duration);
      }
      this.setSelectedDuration(selectedDuration);
    },
    changeDuration: function (item) {
      let tempItem = JSON.parse(JSON.stringify(this.selectedDuration));
      try {
        if(this.loading || item.duration === this.selectedDuration.duration) {
          return
        }
        this.startLoading();
        this.init(item);
      }catch (e) {
        this.init(tempItem);
        this.setErrorMessage(e);
      } finally {
        this.stopLoading();
      }
    }
  },
  mounted() {
    this.init();
  }
}
</script>