<template>
  <v-select solo label="Select Network" :disabled="loading"  v-model="selectedNetwork" @change="selectNetwork" :items="supportedChains" return-object item-text="name">
    </v-select>
</template>
<script>
import {mapGetters, mapMutations} from 'vuex';
export default {
  name: "Instrument",
  computed: {
    ...mapGetters([
      "loading",
      "network",
      "supportedChains"
    ]),
  },
  data: () => {
    return {
        selectedNetwork: null
    }
  },
  methods: {
    ...mapMutations([
        "setSelectedInstrument"
    ]),
    selectNetwork: async function () {
      await this.ethereum.request({method: 'wallet_addEthereumChain', params:[this.selectedNetwork.chainData]});
    }
  },
  mounted() {
    console.log(this.supportedChains);
    this.selectedNetwork = this.network;
  }
}
</script>