                        <!-- <div>
                          <div class="d-inline-flex text-h5 pr-1" :class="(item.price < currentPrice ? 'success--text': 'error--text')">
                            {{item.price < currentPrice ? '+' : ""}}{{getPriceDifference(item)}}
                          </div>
                          <div class="d-inline-flex" :class="(item.price < currentPrice ? 'success--text': 'error--text')">
