                    </v-row>
                    <v-row align="center" justify="center" v-if="item.status === 'Closed' && item.isResolved" class="pb-2">
                      <v-col cols="12" style="border-radius: 5px" class="tertiary" v-if="item.status !== 'Upcoming'">
                        <v-row class="text-left" align="center">
