                <v-row class="px-4" align="center" justify="center">
                    <v-col cols="12">
                      <v-divider></v-divider>
                      <v-row class="py-2" align="center" v-if="item.status === 'Open' && item.playerPrediction === '0.00'">
                        <v-col cols="6" md="4">
