  display: inline-block;
  border: 1px solid #fff;
  padding: 0.2rem 2rem;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
  align-items: center;
  border-radius: 10px;
}
</style>
<script>
import BigNumber from 'bignumber.js';
import RollingNSEBullsEye from "../lib/contracts/rollingNSEBullsEye";
import {mapGetters, mapMutations} from 'vuex';
import moment from "moment";
import {EventBus} from '../store/eventBus';
import {NSE_BULLS_EYE_MARKET_STATUS, NSE_BULLS_EYE_PRICE} from "@/lib/helper/queries";
import nse_holidays from "../chains/nse_holidays";

import VueApexCharts from 'vue-apexcharts'
import { getVuetify } from '@core/utils';

import Instrument from "@/views/Instrument";
import Balance from "@/views/Balance";
import Durations from "@/views/Durations";
import Network from "@/views/Network";

export default {
  name: 'RollingNSEBullsEye',
  components: {
    VueApexCharts,
    Durations,
    Balance,
    Instrument,
    Network
  },
  setup() {
    const $vuetify = getVuetify()
    const chartOptions = {
      chart: {
        type: 'bar',
        toolbar: {
          show: false,
        },
        sparkline: {
          enabled: true,
        },
      },
      grid: {
        show: false,
      },
      colors: [$vuetify.theme.currentTheme.secondary],
      plotOptions: {
        bar: {
          horizontal: false,
          columnWidth: '30%',
          startingShape: 'rounded',
          endingShape: 'rounded',
          borderRadius: 4,
        },
      },
      legend: {
        show: false,
      },
      dataLabels: {
        enabled: false,
      },
      xaxis: {
        labels: {
          show: false,
        },
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
      },
      yaxis: {
        show: false,
      },
      tooltip: {
        x: {
          show: false,
        },
      },
    }
    return {
      chartOptions,
      series: [
        {
          name: '2020',
          data: [-50, 40, 110, 60, 20, -30, 20],
        },
      ]
    }
  },
  data: function(){
    return {
      initialized: false,
      instrument: null,
      client: {},
      swiperOptions: {
        slidesPerView: 2,
        spaceBetween: 50,
        breakpoints: {
          1024: {
            slidesPerView: 2,
            spaceBetween: 40
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 30
          },
          640: {
            slidesPerView: 1,
            spaceBetween: 20
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 20
          }
        },
        centeredSlides: true,
        freeMode: true,
        navigation: false,
        noSwipingSelector: 'button',
        noSwiping: true,
        noSwipingClass: "swiper-no-swiping"
      },
      items: [],
      timeFormat: "dddd, MMMM DD, YYYY hh:mm:ss A",
      utcTime: moment().utc().format("dddd, MMMM DD, YYYY hh:mm:ss A"),
      interval: null,
      progressBarPercentage: 0,
      progressBarRemainingTime: '',
      currentPrice: null,
      trendData: [],
      trendSubcription: null,
      priceSubscription: null,
      marketId: null,
      isBuy: false,
      currentItem: null,
      buySellDialog: false,
      marketData: null,
      marketSubsciptions: {}
    }
  },
  computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",
        "loading",
        "balance",
        "durations",
        "selectedDuration",
        "isLive",
        "isConnected"
    ]),
    swiper() {
      return this.$refs.swiperSlider?.$swiper
    },
  },
  watch: {
    async network(newValue, oldValue) {
      if((!oldValue && newValue) || (oldValue.name !== newValue.name)) {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          if (!this.network) return;
          await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    async getSelectedInstrument(newValue, oldValue) {
      if(oldValue !== newValue) {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          this.startTimer();
          if (!this.network) return;
          //await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    async account(newValue, oldValue) {
      if(oldValue !== newValue) {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          this.startTimer();
          if (!this.network) return;
          //await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    async selectedDuration(newValue, oldValue) {
      if((!oldValue && newValue) || (oldValue.label !== newValue.label))  {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          this.startTimer();
          if (!this.network) return;
          //await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    }
  },
  mounted: async function() {
    try {
    //   EventBus.$on("withdraw", this.withdraw);
    //   EventBus.$on("deposit", this.deposit);
      EventBus.$on("refresh", this.getBalance);
      this.setShowDepositeAndWithdraw(this.isLive);
      this.startLoading();
      if(!this.network || !this.account || !this.getSelectedInstrument) {
        this.setErrorMessage({message: "Please connect to a wallet and select the supported network."})
        return
      }
      this.startTimer();
      if (true == this.initialized) return;
      if (!this.network) return;
      await this.init();
      await this.fetch();
      let idx = this.items.findIndex(v => v.status === "Open")
      this.swiper?.slideTo(idx);
    } catch (e) {
      this.setErrorMessage(e);
    } finally {
      this.stopLoading();
    }
  },
  async destroyed() {
    try {
      await this.client.destroy();
      clearInterval(this.botInterval);
      if(this.trendSubcription) {
        this.trendSubcription.unsubscribe();
      }
    } catch (e) {
      console.log(e)
    }
    // EventBus.$off("withdraw", this.withdraw);
    // EventBus.$off("deposit", this.deposit)
    EventBus.$off("refresh", this.getBalance);
    clearInterval(this.interval);
    this.stopLoading();
  },
  methods: {
    ...mapMutations([
      "startLoading",
      "stopLoading",
      "setBalance",
      "setErrorMessage",
      "setShowDepositeAndWithdraw",
      "setSelectedDuration"
    ]),
    startTimer: function () {
      this.interval = setInterval(this.tick, 1000);
    },
    tick: function() {
      let time = moment().utc();
      let item = this.items.find(v => v.status === "Open");
      if(item && !this.loading) {
        this.progressBarPercentage = ((time.unix() - item.interval)/(moment.unix(item.interval).add(this.selectedDuration.duration, 'seconds').utc().unix() - item.interval)) * 100
        this.progressBarRemainingTime = moment.unix(item.interval).add(this.selectedDuration.duration, 'seconds').utc().fromNow(true);
        if(time.unix() >= moment.unix(item.interval).add(this.selectedDuration.duration, 'seconds').utc().unix()) {
          this.fetch();
        }
      }
      this.utcTime = time.format(this.timeFormat);
    },
    changeDuration: async function(item) {
      let tempItem = JSON.parse(JSON.stringify(this.selectedDuration));
      try {
        if(this.loading || item.duration === this.selectedDuration.duration) {
          return
        }
        this.startLoading();
        this.setSelectedDuration(item)
        await this.fetch();
      }catch (e) {
        this.setSelectedDuration(tempItem);
        this.setErrorMessage(e);
      } finally {
        this.stopLoading();
      }
    },
    init: async function() {
        this.trendData = [];
        this.instrument = this.network.instrument[this.getSelectedInstrument];
        this.client = new RollingNSEBullsEye({
            network: this.network,
            instrument: this.getSelectedInstrument,
            answerListener: this.priceListener,
            account: this.account,
            isLive: this.isLive
        });
        let allowance  = await this.client.init();
        if ((new BigNumber(allowance.toString()).lte(0))) {
          await this.client.approve(new BigNumber(1).shiftedBy(9).toString());
        }
        await this.getBalance();
        if (this.instrument == undefined) return;
        this.setSelectedDuration(this.durations.find(v => v.label === this.instrument.duration));
        this.applicableDurations = this.durations.filter(v => v.duration >= this.selectedDuration.duration);
        await this.getPrice();
    },
    getPrice: async function() {
      if(this.priceSubscription) {
          this.priceSubscription.unsubscribe()
      }
      this.priceSubscription = this.$apollo.subscribe({
          query: NSE_BULLS_EYE_PRICE,
          fetchPolicy: "network-only"
      }).subscribe({
          next: (data) => {
              this.marketData = data.data.markets[0];
              this.currentPrice = this.marketData.display_value;
          },
          error: (error) => {
              console.log(error)
          }
      })
    },
    priceListener: function(current) {
      this.currentPrice = new BigNumber(current.toString()).shiftedBy(-(this.network.instrument[this.getSelectedInstrument].decimals)).toFixed(4)
    },
    getBalance: async function() {
      let balance = await this.client.getBalance(this.account);
      this.setBalance((new BigNumber(balance.toString())).shiftedBy(-(this.network.instrument[this.getSelectedInstrument].decimals)).toFixed(2,  BigNumber.ROUND_DOWN).toString());
    },
    getPriceDifference: function (item) {
      let priceDiff = this.currentPrice - item.price;
      return (new BigNumber(priceDiff)).toFixed(4,  BigNumber.ROUND_DOWN).toString()
    },
    getPriceDifferencePercentage: function (item) {
      let priceDiff = this.getPriceDifference(item);
      let percentageDiff = (priceDiff/item.price)*100
      return (new BigNumber(percentageDiff)).toFixed(2,  BigNumber.ROUND_DOWN).toString()
    },
    moveRight: function () {
      this.swiper.slideNext();
    },
    moveLeft: function () {
      this.swiper.slidePrev();
    },
    // deposit: async function (data) {
    //   try {
    //     if(!data.amount || data.amount <= 0) {
    //       return
    //     }
    //     await this.client.deposit(data.amount);
    //     await this.getBalance();
    //   } catch (e) {
    //     this.setErrorMessage(e);
    //   }
    // },
    // withdraw: async function () {
    //   try {
    //     if(this.balance <= 0) {
    //       return
    //     }
    //     await this.client.withdraw();
    //     await this.getBalance();
    //   } catch (e) {
    //     this.setErrorMessage(e);
    //   }
    // },
    getTime: function(duration = null) {
      const now = new Date().getTime();
      const nowInSeconds = (now - now%1000)/1000;
      if(duration) {
        return (nowInSeconds - nowInSeconds % duration);
      }
      return (nowInSeconds - nowInSeconds % this.selectedDuration.duration);
    },
    getRoundData: async function(interval, time, fromSub = false) {
      let price = "0.0000",
          playerPrediction = "0.00",
          m = '',
          status = "Open",
          eventData = {};
      if(interval < time) {
        status = "Closed"
      }
      if(interval > time) {
        status = "Upcoming"
      }
      if(status !== "Upcoming") {
        eventData = await this.client.getEventDetails(
          moment.unix(interval).add(this.selectedDuration.duration, 'seconds').utc().unix(), 
          moment.unix(interval).add(this.selectedDuration.duration * 2, 'seconds').utc().unix()
        )
        if(eventData && eventData.data && eventData.data.startTime > 0) {
          let p = await this.client.getPlayerPrediction(eventData.id, this.account);
          playerPrediction = (new BigNumber(p.prediction.toString())).shiftedBy(-2).toFixed(2);
          m = eventData.id
          console.log(eventData);
          if(status !== "Open" && !eventData.data.resolved && !fromSub)  {
            this.attachSubscriptionForMarket(m);
          } 
        }
      }
      let endtime = moment.unix(interval).add(this.selectedDuration.duration * 2, 'seconds').format("LLL");
      return {
        status: status,
        label: `What will be the national stock exchange Price at ${endtime}?`,
        challengeStatement: `Current price ${this.currentPrice}`,
        interval: interval,
        market: m,
        price: price,
        time: time,
        isResolved: eventData.data.resolved,
        playerPrediction: playerPrediction
      }
    },
    attachSubscriptionForMarket: async function(marketId) {
      if(this.marketSubsciptions[marketId]) {
        this.marketSubsciptions[marketId].unsubscribe()
      }
      this.marketSubsciptions[marketId] = this.$apollo.subscribe({
          query: NSE_BULLS_EYE_MARKET_STATUS,
          variables: {
            marketId: marketId
          },
          fetchPolicy: "network-only"
      }).subscribe({
          next: async (data) => {
            if(data.data.nse_bulls_eye_rolling_markets[0].status !== "Open") {
              let itemidx = this.items.findIndex(v => {
                v.market == data.data.nse_bulls_eye_rolling_markets[0].market_id
              })
              if(itemidx >= 0) {
                this.items[itemidx] = await this.getRoundData(this.items[itemidx].interval, this.items[itemidx].time, true)
              }
              this.marketSubsciptions[data.data.nse_bulls_eye_rolling_markets[0].market_id].unsubscribe()
            }
            console.log(data)
          },
          error: (error) => {
            console.log(error)
          }
      })
    },
    makePrediction: async function(item) {
      if(!item.prediction || !item.wageredAmount || item.prediction < 0 || item.wageredAmount < 0) {
        return
      }
      this.startLoading();
      try {
        const startTime =  moment.unix(item.interval).add(this.selectedDuration.duration, 'seconds').utc().unix();
        const endTime =  moment.unix(item.interval).add(this.selectedDuration.duration * 2, 'seconds').utc().unix(); 
        const amount = (new BigNumber(item.wageredAmount.toString())).shiftedBy((this.network.instrument[this.getSelectedInstrument].decimals));
        const prediction = (new BigNumber(item.prediction.toString()));//.shiftedBy(2);
        let res = await this.client.makePrediction(startTime, endTime, amount.toString(), prediction.toString());
        item.playerPrediction = item.prediction.toString();
        await this.getBalance();
      } catch(e) {
        this.setErrorMessage(e);
      }
      this.stopLoading()
    },
    fetch: async function(){
      let items = [];
      let promises = [];
      let intervals = [];
      let time = this.getTime()
      for(let i = 2 ; i > 0; i--) {
        intervals.push(moment.unix(time).subtract(this.selectedDuration.duration * (i), 'seconds').utc().unix())
      }
      intervals.push(this.getTime())
      // for(let i = 0 ; i < 1; i++) {
      //   intervals.push(moment.unix(time).add(this.selectedDuration.duration * (i + 1), 'seconds').unix())
      // }
      for (let i = 0; i < intervals.length; i++) {
        promises.push(this.getRoundData(intervals[i], time))
      }
      items = await Promise.all(promises);
      this.items = JSON.parse(JSON.stringify(items.sort((a,b) => {
        return a.interval > b.interval
      })));
      //await this.subscribe();
      this.initialized = true;
    },
      startTimer2: function () {
          this.interval = setInterval(this.tick, 5000);
      },
      tick2: function() {
        let time = moment().utc();
        const isTodayWorkingDay = !this.isHoliday(time) && !this.isWeekend(time);  
        const isThisTradingHour = this.isTradingTime(time);
        let numerator, denominator, openingTime, closingTime, nextWorkingDay, prevClosing;
        if (isTodayWorkingDay){
          openingTime = moment(time).startOf('day').add(3, "hours").add(30, 'minutes');
          closingTime = moment(time).startOf('day').add(10, "hours");
          if (isThisTradingHour){
            this.progressMessage = "Next Window Opens "
            this.progressBarRemainingTime = time.to(closingTime);
            numerator = time.utc() - openingTime.utc();
            denominator = closingTime.utc() - openingTime.utc();
            this.applicableWindow.startTime = '';
            this.applicableWindow.endTime = '';
          }
          else if (time.utc() > closingTime.utc()){
            this.progressMessage = "Window Closes ";
            nextWorkingDay = this.getNextBusinessDay(time);
            this.progressBarRemainingTime = time.to(nextWorkingDay);
            numerator = time.utc() - closingTime.utc();
            denominator = nextWorkingDay.utc() - closingTime.utc();
            this.applicableWindow.startTime = nextWorkingDay.unix();
            this.applicableWindow.endTime = nextWorkingDay.add(6, 'hours').add(30, 'minutes').unix();
          }
          else { //midnight to before opening
            this.progressMessage = "Window Closes ";
            this.progressBarRemainingTime = time.to(openingTime);
            prevClosing = this.getPrevBusinessDay(time);
            numerator = openingTime.utc() - time.utc();
            denominator = openingTime.utc() - prevClosing.utc();
            this.applicableWindow.startTime = openingTime.unix();
            this.applicableWindow.endTime = openingTime.add(6, 'hours').add(30, 'minutes').unix();
          }
          
        }
        else {
          this.progressMessage = "Window Closes ";
          prevClosing = this.getPrevBusinessDay(time);
          nextWorkingDay = this.getNextBusinessDay(time);
          this.progressBarRemainingTime = time.to(nextWorkingDay);
          numerator = time.utc() - prevClosing.utc();
          denominator = nextWorkingDay.utc() - prevClosing.utc();
          this.applicableWindow.startTime = nextWorkingDay.unix();
          this.applicableWindow.endTime = nextWorkingDay.add(6, 'hours').add(30, 'minutes').unix();
        }
        this.progressBarPercentage = (numerator * 100)/denominator;
      },
        getCLosingTime: function(local){
          return moment(local).startOf('day').add(10, 'hours');
        },
        
        getNextBusinessDay: function(local){
          let foundNextBusinessDay = false;
          let nextDay = moment(local);
          while (!foundNextBusinessDay){
            nextDay = nextDay.add(1, "Day");
            if (!this.isWeekend(nextDay) && !this.isHoliday(nextDay)) foundNextBusinessDay = true;
          }
          return nextDay.startOf('day').add(3, 'hours').add(30, 'minutes');
        },
        getPrevBusinessDay: function(local){
          let foundPrevBusinessDay = false;
          let prevDay = moment(local);
          while (!foundPrevBusinessDay){
            if (!this.isWeekend(prevDay) && !this.isHoliday(prevDay)) {
              foundPrevBusinessDay = true;
              break;
            }
            prevDay = prevDay.subtract(1, "Day");
          }
          return prevDay.startOf('day').add(3, 'hours').add(30, 'minutes');
        },
        isWeekend: function(local){
          const localDateTime = local.format("dddd DD-MMM-YYYY HH:mm:ss");
          const tokens = localDateTime.split(" ");
          if ("Saturday" == tokens[0] || "Sunday" == tokens[0]) return true;
          else return false;
        },
        isHoliday: function(local){
          const localDateTime = local.format("dddd DD-MMM-YYYY HH:mm:ss");
          const tokens = localDateTime.split(" ");
          const date = tokens[1];
          const dateTokens = date.split("-");
          const holidayList = nse_holidays[dateTokens[2]];
          return (holidayList.indexOf(date) > -1);
        },
        isTradingTime: function(timeUTC){
          const timeUTCFormatted = timeUTC.format("dddd DD-MMM-YYYY HH:mm:ss");
          const tokens = timeUTCFormatted.split(" ");
          const time = tokens[2];
          const timeTokens = time.split(":");
          if (timeTokens[0] >= 4 && timeTokens[0] < 10) return true;
          if (timeTokens[0] == 3 && timeTokens[1] <= 30) return true;
          return false;
        }
  }
}
</script>
