const BullsEye = require("./bullsEye");
const DelphiERC20 = require('./delphiERC20');

class RollingNSEBullsEye extends BullsEye {
    constructor(options) {
        super(options);
        this.account = options.account;
        // todo add consition for delphi token
        this.isLive = options.isLive;
        if(!this.isLive) {
            this.delphiClient = new DelphiERC20(options);
        }
    }

    async init() {
        await super.init();
        if(this.delphiClient) {
            await this.delphiClient.init();
            return await this.delphiClient.signerContract.allowance(this.account, this.contractAddress);
        }
        
    }

    async approve(amount) {
        if(this.delphiClient) {
            const withSigner = this.delphiClient.signerContract.connect(this.delphiClient.metamaskProvider.getSigner());
            await withSigner.approve(this.contractAddress, amount, {gasLimit: 2000000});
        }
    }

    async getBalance() {
        if(this.delphiClient) {
            return await this.delphiClient.getBalance(this.account)
        }
     }

     async makePrediction(startTime, endTime, amount, userPrediction) {
        const bandsAndPayouts = [10000, 20000, 40000, 80000];
        const createCommissionBasisPoints = 0;
        const resolution = 1;
        return await super.makePrediction(
            startTime,
            endTime,
            'NSE-RollingBullsEye', 
            bandsAndPayouts, 
            createCommissionBasisPoints, 
            resolution, 
            //this.isLive ? this.contractAddress : this.network.instrument[this.instrument].oracle, 
            '0x0000000000000000000000000000000000000000',
            !!this.isLive, 
            amount, 
            userPrediction, 
            true
        );
     }

     async getEventDetails(startTime, endTime) {
        return await super.getEventDetails(startTime, endTime, 'NSE-RollingBullsEye', '0x0000000000000000000000000000000000000000')
     }

     async getPlayerPrediction(eventID, playerId) {
        return await super.getPlayerPrediction(eventID, playerId, 0)
    }

}

module.exports = RollingNSEBullsEye;