const {gql} = require('graphql-tag');

const BUY_SELL_EVENT_TRADABLE_ROLLING=gql`
    subscription buySellTradableOddsRolling($marketId: String!) {
      buy_sell_trend_rolling_tradable_odds(where: {buy_sell_trend_rolling_tradable_odds_market: {market_id: {_eq: $marketId}}}, order_by: {created_at: desc}) {
        no_coin_price
        no_pool_size
        yes_coin_price
        yes_pool_size
        created_at
        updated_at
        market_id
        buy_sell_trend_rolling_tradable_odds_market {
          market_id
        }
      }
    }
`

const NSE_BULLS_EYE_PRICE = gql`
  subscription getNSEMarketPrice {
      markets(where: {code: {_eq: "nse"}}) {
        code
        display_value
        value
      }
  }
`

const NSE_BULLS_EYE_MARKET_STATUS = gql`
  subscription getBullsEyeRollingMarket($marketId: String!) {
    nse_bulls_eye_rolling_markets(where: {market_id: {_eq: $marketId}}) {
      status
      market_id
    }
  }
`


export {
    BUY_SELL_EVENT_TRADABLE_ROLLING,
    NSE_BULLS_EYE_PRICE,
    NSE_BULLS_EYE_MARKET_STATUS
}