<template>
  <v-select solo v-if="network && network.instrument" label="Select Instrument" :disabled="loading" :value="getSelectedInstrument" @change="setSelectedInstrument" :items="options">
  </v-select>
</template>
<script>
import {mapGetters, mapMutations} from 'vuex';
export default {
  name: "Instrument",
  computed: {
    ...mapGetters([
      "getSelectedInstrument",
      "loading",
      "network"
    ]),
  },
  data: () => {
    return {
      options: []
    }
  },
  methods: {
    ...mapMutations([
        "setSelectedInstrument"
    ]),
    setOptions: function () {
      if(this.network.instrument) {
        for (const key of Object.keys(this.network.instrument)) {
          this.options.push(key)
        }
      }
    }
  },
  watch: {
    network(oldValue, newValue) {
      if (oldValue !== newValue) {
        this.setOptions();
      }
    }
  },
  mounted() {
    this.setOptions();
  }
}
</script>