<template>
  <v-row>
    <v-col cols="12">
      <v-tooltip bottom v-if="showDepositeAndWithdraw">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
              icon
              fab
              color="success"
              class="mr-2"
              v-bind="attrs"
              v-on="on"
              @click="depositModal = true"
              :disabled="loading"
          >
            <v-icon large>mdi-bank-transfer-in</v-icon>
          </v-btn>
        </template>
        <span>Deposit</span>
      </v-tooltip>
      <v-btn
          text
          color="primary"
          :disabled="loading"
      >
        Balance: {{ balance }}
        <v-icon
            color="black"
            right
            dark
            @click="refreshBalance"
        >
          mdi-refresh
        </v-icon>
      </v-btn>
      <v-tooltip bottom v-if="showDepositeAndWithdraw">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
              icon
              fab
              color="info"
              v-bind="attrs"
              v-on="on"
              @click="withdraw()"
              :disabled="loading"
          >
            <v-icon large>mdi-bank-transfer-out</v-icon>
          </v-btn>
        </template>
        <span>Withdraw</span>
      </v-tooltip>
    </v-col>
    <v-col cols="12">
      <v-dialog
          transition="dialog-bottom-transition"
          max-width="600"
          v-model="depositModal"
      >
        <template v-if="network && network.chainData && network.chainData.nativeCurrency">
          <v-card>
            <v-card-title>
              Deposit {{network.chainData.nativeCurrency.symbol}}
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                        type="number"
                        :label="network.chainData.nativeCurrency.symbol"
                        v-model="amount"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn
                  color="primary"
                  @click="deposit()"
              >
                Deposit
              </v-btn>
              <v-btn
                  text
                  @click="depositModal = false"
              >Close</v-btn>
            </v-card-actions>
          </v-card>
        </template>
      </v-dialog>
    </v-col>
  </v-row>
</template>
<script>
import {mapMutations, mapGetters} from "vuex";
import {EventBus} from "@/store/eventBus";
import BigNumber from "bignumber.js";

export default {
  name: 'Balance',
  computed: {
    ...mapGetters([
        "loading",
        "balance",
        "network",
        "showDepositeAndWithdraw"
    ])
  },
  data: function () {
    return {
      depositModal: false,
      amount: 0
    }
  },
  methods: {
    refreshBalance: function () {
      EventBus.$emit('refresh');
    },
    withdraw: function () {
      EventBus.$emit('withdraw')
    },
    deposit: function () {
      if(this.amount <= 0) {
        return
      }
      let amount = (new BigNumber(this.amount)).shiftedBy(this.network.chainData.nativeCurrency.decimals).toString();
      EventBus.$emit('deposit', {"amount": amount})
      this.depositModal = false;
    }
  }
}
</script>